@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
.login-background {
  /* background: #F4F9F5 0% 0% no-repeat padding-box; */
}
.mainPage {
  height: 100vh;
  justify-content: center !important;
}

.birbal-logo-new {
  display: none;
}

.div-wrapper {
  display: flex;
  justify-content: space-between;
}

.div-1-wrapper {
  width: 50%;
}

.div-2-wrapper {
  width: 40%;
}

.logo-container {
  margin-right: 50px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  align-items: center;
  margin-top: 20px;
}

.logo-container img {
  width: 20%;
  height: 20%;
}
.login-text-main {
  margin-left: 40px;
}

.login-text {
  color: #04724d;
  font-size: 50px;
  margin-bottom: 0px;
  font-weight: bold;
}
.para-text {
  font-size: 20px;
  margin-top: 0px;
  color: #000000;
}

.loginform-div {
  margin-top: 105px;
  /* padding: 0 50px 50px 50px; */
  display: flex;
  justify-content: center;
  align-items: center;
}

.loginform-div .MuiInputBase-root {
  width: 100%;
  font-size: 14px;
  border-radius: 20px;
  padding: 10px 20px;
  background-color: #a9c9b2;
}

.loginform-div .MuiFormControl-root {
  width: 100%;
}

.loginform-div .MuiSvgIcon-root {
  width: 1.2em;
  height: 1.2em;
  color: black;
}

.loginform-div .MuiInput-underline:before {
  border-bottom: none !important;
}

.text-field-input {
  padding: 0px 20px;
}

.loginform-div-1 {
  margin-top: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.loginform-div-1 .MuiInputBase-root {
  width: 100%;
  font-size: 14px;
  border-radius: 20px;
  padding: 10px 20px;
  background-color: #a9c9b2;
}

.upload-file-div,
.input {
  /* width: 100%; */
  width: 15.5%;
  font-size: 14px;
  border-radius: 20px;
  padding: 13px 20px;
  background-color: #a9c9b2;
}
.loginform-div-1 .MuiInput-underline:before {
  border-bottom: none !important;
}

.loginform-div-1 .MuiFormControl-root {
  width: 100%;
}

.loginform-div-1 .MuiSvgIcon-root {
  width: 1.2em;
  height: 1.2em;
  color: black;
}

.button-div {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 20px 0;
}

.button-div .MuiButton-contained {
  background-color: #04724d;
  color: #ffffff;
  padding: 10px;
  width: 200px;
}

.upload-file .MuiButton-contained {
  font-size: 14px;
  border-radius: 20px;
  padding: 13px 48px;
  background-color: #a9c9b2;
  width: 242px;
  margin-top: 5px;
  /* margin-top: -27px; */
}

.upload-file .MuiButton-contained:hover {
  box-shadow: 0px 2px 4px -1px rgb(0 0 0 / 20%),
    0px 4px 5px 0px rgb(0 0 0 / 14%), 0px 1px 10px 0px rgb(0 0 0 / 12%);
  background-color: #a9c9b2;
}

.button-div .MuiButton-contained:hover {
  background-color: #04724d;
}

@media screen and (max-width: 425px) {
  .upload-file .MuiButton-contained {
    padding: 13px 0px;
    width: 186px;
  }
}

.log-home {
  padding-top: 80px;
  height: 100%;
  background: #f4f9f5 0% 0% no-repeat padding-box;
}
.login_instr h6 {
  font-size: 40px;
  color: #04724d;
  margin-bottom: 30px;
}
.login_instr p {
  font-size: 14px;
  color: #000000;
  margin: 0;
}
.login-form .card {
  background: #ffffff 0% 0% no-repeat padding-box;
}
.input-icons i {
  padding: 6px;
  position: absolute;
}
.input-field {
  padding-left: 20px;
}
.login-form .card {
  padding: 20px;
}
.MuiInput-underline:before {
  border: none !important;
}
.MuiInput-underline:after {
  border: none !important;
}
.input-form {
  display: block;
  justify-content: space-between;
}
.log-img {
  width: 100%;
  height: 145px;
}
/* .form-group {
  padding: 10px 0px;
} */
.login_instr {
  margin-top: 80px;
}
.upload-file {
  justify-content: center;
  display: flex;
  margin-bottom: 20px;
}
.MuiInput-root {
  width: 100%;
}
.login-bold-text {
  font-weight: bold;
  /* font-family: Ubuntu; */
  font-size: 46px;
  color: #ffffff;
  position: absolute;
  bottom: 0;
  left: 20px;

  opacity: 1;
  z-index: 1;
}
.img-login {
  position: relative;
}

.error {
  padding: 10px 0;
  font-size: 12px;
  color: red;
}

.list {
}
.MessageDiv {
  padding: 20px 50px;
  position: relative;
  display: flex;
  justify-content: center;
  height: 100vh;
}
.messageArea {
  background-color: #fff;
  width: 50%;
  position: absolute;
  top: 35%;
  padding: 40px;
  text-align: center;
  font-weight: 600;
  font-size: 19px;
  color: #000000;
  opacity: 1;
  border-radius: 4px;
}
.birbal-logo1 {
  position: absolute;
  top: 20px;
  right: 50px;
}
.birbal-logo1 img {
  width: 120px;
}

.point-wrapper {
  display: flex;
  /* justify-content: center; */
  align-items: center;
  padding: 15px 0;
}

.point-wrapper p {
  font-size: 16px;
  padding: 0 20px;
}

.bullet {
  border-radius: 50%;
  background-color: #04724d;
  width: 20px;
  height: 20px;
}
.point-content {
  width: 90%;
}

.MuiInputBase-input {
  padding: 6px 10px 7px !important;
}

.upload-photo {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px dashed #696969;
  border-radius: 8px;
  padding: 20px;
}

.upload-photo img {
  width: 20px;
  height: 20px;
}

.flex-div {
  display: flex;
  justify-content: center;
  align-items: center;
}

.text-upload {
  padding: 0 10px;
  color: #04724d;
  font-size: 14px;
}

@media (max-width: 1024px) {
  .form-group {
    margin-bottom: 5px !important;
  }
  .upload-photo {
    padding: 10px;
  }
  .birbal-logo {
    display: none;
  }
  .birbal-logo-new {
    display: block;
    width: 100px;
    padding-bottom: 20px;
  }

  .birbal-logo-new img {
    width: 100%;
  }

  .log-home {
    padding-top: 20px;
  }

  .login_instr {
    margin-top: 20px;
  }

  .div-wrapper {
    display: flex;
    flex-direction: column-reverse;
  }

  .div-1-wrapper {
    width: 100%;
  }

  .div-2-wrapper {
    width: 100%;
  }

  label {
    font-size: 13px;
    font-weight: 600;
  }
  .MuiInputBase-input {
    font-size: 13px !important;
    font-weight: 600 !important;
  }
  .login_instr {
    background: #fff;
    padding: 20px;
  }
  .login_instr h6 {
    font-size: 23px;
    font-weight: bold;
    margin-bottom: 15px;
  }
  .bullet {
    width: 15px;
    height: 15px;
  }
  .point-wrapper {
    padding: 10px 0;
  }
  .point-wrapper p {
    font-size: 12px;
    /* padding: 0 10px; */
  }
  .login-bold-text {
    font-size: 29px;
  }
}

